:root {
	--selected-background: #34495e;
	--selected-color: #fff;
	--disabled-color: #bbb;
	--component-background: #fff;
	--component-color: #424242;
	--underlay-background: rgba(66, 66, 66, 0.3);
	--underlay-background: rgba(66, 66, 66, 0.3);

	--error-color: red;
	--success-color: green;

	--short-animation-duration: .2s;
	--short-animation-easing: ease;

	/* Z-index levels */
	--zindex-base: 0;
	--zindex-tooltip: 100;
	--zindex-dropdown: 200;
	--zindex-fixed: 300;
	--zindex-dialog: 400;
	--zindex-alert: 500;
}

.root {}

.inputWrapper {
	display: inline-block;
	position: relative;
}

/* custom input styles */

.trigger {
	background: #fff;
	background: var(--component-background);
	border: 1px solid #424242;
	border: 1px solid var(--component-color);
	font-size: inherit;
	min-height: 25px;
	min-width: 200px;
	padding-right: 25px;
	position: relative;
	text-align: left;
}

.trigger i {
	position: absolute;
	right: 4px;
}

.trigger:focus {
	color: #34495e;
	color: var(--selected-background);
}

.placeholder {}

.required {}

.dropdown {
	background: #fff;
	background: var(--component-background);
	border: 1px solid #424242;
	border: 1px solid var(--component-color);
	display: none;
	max-height: 400px;
	overflow-y: auto;
	position: absolute;
	width: 100%;
}

.open {}

.open .dropdown {
	display: block;
	z-index: 200;
	z-index: var(--zindex-dropdown);
}

/* native input styles */

.input {
	-webkit-appearance: none;
	   -moz-appearance: none;
	        appearance: none;
	background: #fff;
	background: var(--component-background);
	border: 1px solid #424242;
	border: 1px solid var(--component-color);
	border-radius: 0;
	cursor: pointer;
	display: block;
	font-size: inherit;
	line-height: 1;
	max-width: 100%;
	padding: 2px 25px 2px 4px;
	-webkit-transition: border .2s ease;
	transition: border .2s ease;
	-webkit-transition: border var(--short-animation-duration) ease;
	transition: border var(--short-animation-duration) ease;
	width: 200px;
}

.input:focus {
	border-color: #34495e;
	border-color: var(--selected-background);
}

.arrow {
	bottom: 0;
	pointer-events: none;
	position: absolute;
	right: 0;
	top: 0;
	width: 1.5em;
}

.arrow i {
	position: absolute;
	right: 4px;
	padding: 2px 0;
}

.input:focus ~ .arrow {
	color: #34495e;
	color: var(--selected-background);
}

/* State classes */

.focused { }

.disabled .input,
.readonly .input {
	background-color: #bbb;
	background-color: var(--disabled-color);
}

.invalid .input {
	border-color: red;
	border-color: var(--error-color);
}

.invalid .arrow {
	background-color: red;
	background-color: var(--error-color);
}

.required { }

.valid .input {
	border-color: green;
	border-color: var(--success-color);
}

.valid .arrow {
	background-color: green;
	background-color: var(--success-color);
}



